<template>
      <div class="row">

    <div class="col-sm">
 
      <h3>Trade</h3>

      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Account Balance</span>
        </div>
        <input v-model.number="balance" type="number" class="form-control"  aria-label="Username" aria-describedby="basic-addon1">
      </div>

      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Risk</span>
        </div>
        <input v-model.number="account.risk" type="number" class="form-control"  aria-label="Username" aria-describedby="basic-addon1">
      </div>

       <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Leverage</span>
        </div>
        <input v-model.number="account.leverage" type="number" class="form-control"  aria-label="Username" aria-describedby="basic-addon1">
      </div>

       <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Pair</span>
        </div>
        <input v-model="account.pair" type="text" class="form-control"  aria-label="Username" aria-describedby="basic-addon1" style="text-transform: uppercase;">
      </div>

      <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Invalidation Price</span>
        </div>
        <input v-model.number="invalidation" type="number" class="form-control"  aria-label="Username" aria-describedby="basic-addon1">
      </div>




    </div>


    <div class="col-sm">
     <h3>Entries &#38; Exits </h3> 

  
  

     <div v-for="(entry, index) in entries" :key="'entry' + index">
       <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Entry {{index + 1}}</span>
        </div>
        <input v-model.number="entry.price" type="number" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
        
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">% </span>
        </div>
        <input v-model.number="entry.percent" type="number" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
      
      </div>
</div>
<div class="mx-1 my-4" >Average Entry Price: {{averageEntryPrice()}}</div>

        <button type="button" class="btn btn-outline-success mx-1 mb-3" @click="addEntry()">Add Entry  </button>
      <button type="button" class="btn btn-outline-danger mx-1 mb-3" @click="popEntry()">Delete Entry</button>


      


     <!-- <h3>Exits</h3> -->
<div v-for="(exit, index) in exits" :key="'exit' + index">
       <div class="input-group mb-3">
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">Exit {{index + 1}}</span>
        </div>
        <input v-model.number="exit.price"  type="number" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
        
        <div class="input-group-prepend">
          <span class="input-group-text" id="basic-addon1">% </span>
        </div>
        <input v-model.number="exit.percent" type="number" class="form-control" aria-label="Username" aria-describedby="basic-addon1">
      
      </div>
</div>
<div class="mx-1 my-4" >Average Exit Price: {{averageEntryPrice()}}</div>

      <button type="button" class="btn btn-outline-success mx-1" @click="addExit()">Add Exit</button>
      <button type="button" class="btn btn-outline-danger mx-1" @click="popExit()">Delete Exit</button>




    </div>


        <div class="col-sm">

     <h3>Calculations</h3>



 <div class="card ">
  
 
      <table class="table table-striped my-0 ">
  
  <tbody>
       <tr>
    
      <td>Position Side</td>
      <td>{{info.Side}}</td>
    </tr>
    <tr>
    
      <td>% Change to SL</td>
      <td>{{info.PercentageChangeToSL.toFixed(2)}}</td>
    </tr>
    <tr>
     
      <td>% Change to TP</td>
      <td>
        {{info.PercentageChangeToTP}}
      </td>
    </tr>
    <tr>
    
      <td>Total Margin</td>
      <td> {{info.Margin}} </td>
    </tr>
    <tr>
    
      <td>Total Risk</td>
      <td> {{info.Risk}} </td>
    </tr>
    <tr>
    
      <td>RR</td>
      <td> {{info.RR}} </td>
    </tr>
    <tr>
    
      <td>Reward</td>
      <td> {{info.Reward}} </td>
    </tr>
 
    <tr>
          <tr>
    
      <td>Liquidation</td>
      <td> {{info.LiquidationPrice}} </td>
    </tr>
 
    <tr>
    
      <td>Total Size</td>
      <td> {{info.TotalSize}} </td>
    </tr>
   
   <tr v-if="info.LiquidationTriggersFirst == true" >
   <td colspan="2" >
       <div  class="alert alert-warning calc-warning mb-0 " > 
                Liquidation will happen before Stop Loss gets triggered
        </div>

   </td>

   </tr>

     
       
  
   
 
  </tbody>
</table>
 
</div>

       



    </div>
  </div>
</template>


<script>


export default({
    data(){
        return{
                balance: 0,
                entries: [{price:0, percent: 100}],
                exits: [{price:0, percent: 100}],
                account: {leverage:0, risk:0, pair: 0},
                exchange: "binance",
                mode: "limit",
                invalidation: 0,
                           
                //State
                loading: true,
                calculating: false,
                placing_trade: false
        }
    },
    methods:{
    addEntry(){
      this.entries.push({price:0, percent: 0})
    }, 
    addExit(){
      this.exits.push({price:0, percent: 0})
    },
    popEntry(){
      this.entries.pop()
    }, 
    popExit(){
      this.exits.pop()
    },
   

    averageEntryPrice(){
        return this.entries.reduce((total, next) => total + next.price * next.percent/100, 0) ;
       
    },
    averageExitPrice(){
        return this.exits.reduce((total, next) => total + next.price * next.percent/100, 0) ;
        
    }
    },
    computed:{
        info: function(){
            let avgEntry = this.averageEntryPrice()
            let avgExit = this.averageExitPrice()
            let PositionSide = -1
            let Side = "LONG"
            if (avgEntry > avgExit) {PositionSide = 1; Side = "SHORT"}
           
            let LiquidationPrice = avgEntry + PositionSide * avgEntry * 1/this.account.leverage 

            let LiquidationTriggersFirst = false
            if(Side=="SHORT"){
                if(LiquidationPrice < this.invalidation) LiquidationTriggersFirst = true
            }
            else if(Side=="LONG"){
                if(LiquidationPrice > this.invalidation) LiquidationTriggersFirst = true
            }

            let balance = this.balance
            let Risk = ( balance * this.account.risk / 100).toFixed(2)
            let PercentageChangeToSL = Math.abs(avgEntry - this.invalidation) / avgEntry*100 
            let PercentageChangeToTP = (( avgEntry - avgExit ) / avgEntry )*100
            let Margin = 100* Risk / Math.abs( PercentageChangeToSL * this.account.leverage)
            let Reward = Math.abs(Margin * this.account.leverage * PercentageChangeToTP)
            let RR = Reward / Risk
            let TotalSize = (this.account.leverage * Margin) / avgEntry
            return  {
                PercentageChangeToSL: PercentageChangeToSL,
                PercentageChangeToTP: PercentageChangeToTP.toFixed(2) ,
                RR: RR.toFixed(2),
                Reward: Reward.toFixed(2),
                Risk: Risk ,
                Margin: Margin.toFixed(2) ,
                TotalSize: TotalSize.toFixed(5),
                LiquidationPrice: LiquidationPrice.toFixed(4),
                Side: Side,
                LiquidationTriggersFirst: LiquidationTriggersFirst
                }
        }
    },
    setup() {
        
    },
})
</script>


